//COLORS
$white: white;
$point: #0a70d3;
$dark-blue: #003675;
$deep-dark-blue: #011d3f;
$skyblue: #009bd5;

$red: #da2576;
$green: #0C7F3A;
$line: #6f6f6f;

$grey-222: #222;
$grey-333: #333;
$grey-444: #444;
$grey-666: #666;
$grey-999: #999;
$grey-000: #000;
$grey-ccc: #ccc;
$grey-ddd: #ddd;
$grey-eee: #EEE;
$disabled: $grey-eee;
$black: $grey-222;
$white: #fff;

$bg-hover: #EEF2F7;
$bg-active: #D6E0EB;

// FONTs
$font: 'Pretendard';
$font-2: 'GmarketSansMedium';

$radius: 5px;

//CONTENTS MAX WIDTH
$contents-size: 1280px !default;

//BREAK POINTS
$breakpoint-small: 360px !default;
$breakpoint-medium: 768px !default;
$breakpoint-large: 1024px !default;
$breakpoint-xlarge: 1280px !default;
$breakpoint-xxlarge: 1440px !default;

//SCREEN ZOOM
$screen-zoom-sizes: (
        small: 0.9,
        medium: 1,
        large: 1.1,
        xlarge: 1.3,
        xxlarge: 1.5,
) !default;