/*
  Config
*/

@mixin img {
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

@mixin ellipsis($line, $line-height: 18px) {
  @if ($line ==1) {
    display: block;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
    width: 100%;
    overflow: hidden;
  }

  @else {
    display: block;
    display: -webkit-box;
    overflow: hidden;
    line-height: $line-height;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $line;
  }
}

@mixin scrollbar {
  ::-webkit-scrollbar {
    width: 5px;
    height: 5px;
  }

  ::-webkit-scrollbar-track {
    background-color: #ddd;
  }

  ::-webkit-scrollbar-thumb {
    background-color: #c1c1c1;
    border-radius: 5px;
  }
}