/*
  MAIN
*/

@use "variables" as *;
@use "config" as *;

.main-contents {
  // --------------------------- 메인 공통 ---------------------------
  .section-title {
    font-size: 30px;
    font-weight: bold;
    letter-spacing: -1.5px;
  }

  .btn-more {
    display: flex;
    align-items: center;
    gap: 5px;
    justify-content: center;
    padding: 5px;
    border-radius: 6px;
    transition: .2s;
    font-size: 16px;
    letter-spacing: -0.8px;

    &:hover {
      background-color: $bg-hover;
    }

    &:active {
      background-color: $bg-active;
    }
  }

  .btn-square-more {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10px;
    height: 30px;
    line-height: 28px;
    transition: .2s;
    font-size: 15px;
    letter-spacing: -0.75px;
    border-radius: 2px;
    background-color: $white;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
    border: solid 1px $grey-ccc;
    width: 100%;

    &:hover {
      background-color: $bg-hover;
    }

    &:active {
      background-color: $bg-active;
    }
  }

  .slider-navigation {
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .progress {
    position: relative;
    width: 30px;
    height: 30px;
    margin-left: 15px;

    .bar {
      position: absolute;
      left: 0;
      top: 0;
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: bold;
      color: #000;

      .rolling {
        --progress: 0;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        stroke-width: 2px;
        stroke: #000;
        fill: none;
        stroke-dashoffset: calc(125.6 * (1 - var(--progress)));
        stroke-dasharray: 125.6;
        transform: rotate(-90deg);
        z-index: 1;
      }

      .track {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        stroke-width: 2px;
        stroke: #cccccc;
        fill: none;
      }
    }
  }

  .page-num {
    display: flex;
    align-items: center;
    margin: -2px 16px 0;
    width: auto;

    .current {
      font-size: 14px;
      font-weight: bold;
      color: #1d56bc;
    }

    .total {
      position: relative;
      font-size: 14px;
      letter-spacing: -0.65px;
      color: $grey-666;
      margin-left: 13px;

      &::before {
        content: '/';
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -8px;
      }
    }
  }

  .btn-slide-state {
    position: relative;
    z-index: 2;
    background-color: transparent;
    border: 0;
    font-size: 10px;
    cursor: pointer;
    padding-top: 2px;

    .ico-slider-play {
      transform: translateX(1px);
    }
  }

  .section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
  }

  .section-filter {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 30px;

    .item {
      position: relative;
      font-size: 20px;
      line-height: 24px;
      letter-spacing: -1px;
      color: $grey-444;

      &.on {
        > * {
          font-weight: bold;
          color: $grey-222;
        }

        &::before {
          content: '';
          position: absolute;
          top: -6px;
          left: 50%;
          transform: translateY(-50%);
          width: 6px;
          height: 6px;
          border-radius: 100%;
          background-color: #e4022e;
        }
      }
    }
  }
  // ---------------------------------------------------------------
  .section-visual {
    padding: 50px 0;

    .row {
      display: flex;
    }

    .box-banner {
      position: relative;
      width: 50%;
      height: fit-content;

      .banner-slider {
        width: 100%;

        .link {
          display: block;
          aspect-ratio: 740 / 417;
          overflow: hidden;
          border-radius: 10px;

          &:focus, &:focus-visible {
            outline-offset: -4px;
            outline: 2px solid $grey-222;
            border-radius: 10px;
          }

          img {
            width: 100%;
          }
        }
      }

      .slider-controls {
        position: absolute;
        bottom: 20px;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        justify-content: center;
        align-items: center;
        width: 120px;
        height: 30px;
        border-radius: 15px;
        background-color: rgba(7, 21, 47, .8);
        z-index: 2;

        .btn-arrow-prev, .btn-arrow-next {
          display: flex;
          justify-content: center;
          align-items: center;
          width: auto;
          height: 30px;
        }

        .page-num {
          padding-top: 4px;

          .current {
            font-size: 16px;
            font-weight: 500;
            color: $white;
          }

          .total {
            font-size: 16px;
            color: $grey-ccc;
          }
        }
      }

      .btn-slide-state {
        position: absolute;
        right: 20px;
        bottom: 20px;
        width: 30px;
        height: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(7, 21, 47, .8);
        border-radius: 100%;
        padding-bottom: 2px;
      }
    }

    .box-search {
      display: flex;
      flex-direction: column;
      width: 50%;
      padding-left: 50px;
      padding-right: 30px;
      padding-top: 5px;

      .search-header {
        display: flex;
        flex-wrap: wrap;
        gap: 14px;
        margin-bottom: 15px;

        .txt-box {
          .txt {
            font-size: 20px;
            font-weight: 500;
            color: $grey-444;
            line-height: 27px;
            margin-bottom: 2px;
          }

          .txt-lg {
            font-size: 34px;
            line-height: 40px;
            font-weight: bold;
          }
        }

        img {
          display: inline-flex;
          width: 48px;
          height: 68px;
        }
      }

      .main-search {
        position: relative;
        margin-bottom: 20px;

        .form-input {
          width: 100%;
          height: 60px;
          padding: 0 50px 0 20px;
          border-radius: 5px;
          background-color: #f9f9f9;
          border: 3px solid #1d56bc;
          font-size: 20px;
          letter-spacing: -1px;

          &::placeholder {
            color: $grey-666;
          }
        }

        .btn-search {
          position: absolute;
          display: flex;
          justify-content: center;
          align-items: center;
          right: 0;
          top: 0;
          width: 60px;
          height: 60px;
        }
      }

      .tag-list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        margin-bottom: 15px;

        .tag {
          display: inline-flex;
          height: 34px;
          line-height: 34px;
          border-radius: 5px;
          background-color: #003675;
          padding: 0 15px;
          font-size: 16px;
          font-weight: 500;
          color: $white;
        }
      }

      .desc {
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.8px;
        color: $grey-444;
        margin-bottom: 25px;
      }

      .partner-list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;

        .partner {
          display: inline-flex;
          height: 50px;

          img {
            width: auto;
            height: 100%;
          }
        }
      }
    }
  }

  .section-recommend {
    padding: 50px 0 70px;
    background-color: #f8f8f8;

    .section-body {
      position: relative;
    }
  }

  .section-exhibition {
    padding: 50px 0;

    .section-body {
      position: relative;
    }
  }

  .section-education {
    position: relative;
    padding: 50px 0;
    background-image: url(../images/main/bg-section-edu.png);
    background-size: 1800px 440px;
    background-repeat: no-repeat;
    background-position: right top;

    .section-body {
      position: relative;
    }

    &::before {
      content: '';
      position: absolute;
      right: 0;
      top: 145px;
      width: 223px;
      height: 148px;
      background-image: url(../images/main/bg-section-deco.png);
      background-size: cover;
    }
  }

  .section-meta {
    position: relative;
    padding: 50px 0;
    background-image: url(../images/main/bg-meta.png);
    background-size: 1600px 500px;
    background-repeat: no-repeat;
    background-position: left top;

    .row {
      display: flex;
    }

    .box-map {
      width: 50%;

      .map {
        position: relative;
        margin: 0 auto;

        .btn-marker {
          position: absolute;
          display: block;
          font-weight: 500;
          font-size: 16px;
          letter-spacing: -.8px;
          padding: 0 15px;
          color: $white;
          height: 34px;
          line-height: 34px;
          border-radius: 5px;
          box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.2);
          background-color: #003675;
          transition: .2s;
          white-space: nowrap;

          &::after {
            content: '';
            transition: .2s;
            position: absolute;
            width: 6px;
            height: 10px;
            background-image: url(../images/main/ico-mark-arrow.png);
            background-size: cover;
            opacity: 0;
            top: 50%;
            transform: translateY(-50%);
            right: 12px;
          }

          &:hover {
            background-color: #1d56bc;
            padding: 0 25px 0 15px;

            &::after {
              opacity: 1;
            }
          }

          &.location-1 {
            top: 116px;
            left: 205px;
          }

          &.location-2 {
            top: 188px;
            left: 24px;
          }

          &.location-3 {
            top: 264px;
            left: 262px;
          }

          &.location-4 {
            top: 324px;
            left: 55px;
          }

          &.location-5 {
            top: 417px;
            left: 155px;
          }
        }
      }
    }

    .box-meta {
      position: relative;
      width: 50%;
      padding-left: 60px;
      padding-right: 120px;

      .title {
        font-size: 26px;
        font-weight: bold;
        line-height: 30px;
        letter-spacing: -1.3px;
        color: #1d56bc;
        margin-bottom: 7px;
      }

      .desc {
        font-size: 15px;
        line-height: 23px;
        color: $grey-444;
        margin-bottom: 15px;
      }

      .slider-wrapper {
        position: relative;
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2);
        border-radius: 10px;
        padding-right: 0;
      }

      .meta-slider {
        .swiper-slide {
          opacity: 0;

          &.swiper-slide-active {
            opacity: 1;
          }
        }

        .link {
          display: block;
          border-radius: 10px;
          aspect-ratio: 460 / 260;
          overflow: hidden;

          img {
            display: block;
            width: 100%;
            height: 100%;
          }
        }
      }

      .btn-circle-prev, .btn-circle-next {
        width: 40px;
        height: 40px;
        border-color: #afcad2;
      }

      .btn-circle-prev {
        left: -60px;
      }

      .btn-circle-next {
        right: -60px;
      }
    }
  }

  .section-notice {
    padding-top: 55px;

    .row {
      display: flex;
    }

    .tab-container {
      .tabs-box {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 5px;
        margin-bottom: 28px;

        .tabs {
          .item {
            height: 82px;

            .tab-button {
              font-size: 26px;
              letter-spacing: -1.3px;
              font-weight: 500;
              padding: 0 25px;
              transform-origin: center;
            }

            &.on {
              .tab-button {
                font-weight: bold;
                letter-spacing: -1.5px;
                font-size: 30px;
              }
            }
          }
        }
      }
    }

    .btns {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      height: 82px;
      border-bottom: 2px solid $grey-ccc;
    }

    .alert-list {
      .item {
        + .item {
          margin-top: 10px;
        }

        a {
          display: flex;
          align-items: center;
          padding: 10px 0;
          gap: 15px;
          
          &:hover {
            .txt {
              text-decoration: underline;
            }
          }
        }

        .badge {
          flex-shrink: 0;
          width: 50px;
          height: 25px;
          border-radius: 2px;
          background-color: #f0f0f0;
          border: 1px solid $grey-ccc;
          line-height: 24px;
          font-size: 15px;
          font-weight: 500;
          color: $grey-222;
          letter-spacing: -.75px;

          &.urgent {
            background-color: #e8effa;
            border-color: #aec1de;
            color: #1d56bc;
          }
        }

        .txt {
          max-width: calc(100% - 50px - 15px - 15px - 70px);
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          font-size: 17px;
          font-weight: 500;
          letter-spacing: -.85px;
        }

        .date {
          flex-shrink: 0;
          font-size: 16px;
          font-weight: 300;
          color: $grey-666;
          letter-spacing: -.8px;
          margin-left: auto;
        }
      }
    }

    .box-notice {
      width: 50%;
      border-bottom: 1px solid $grey-ccc;
    }

    .box-event {
      width: calc(50% - 50px);
      margin-left: 50px;
      padding-right: 10px;

      .title-head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 82px;
      }
    }

    .slider-wrapper {
      .swiper-slide {
        border-radius: 10px;
        overflow: hidden;

        .link {
          display: block;
          width: 100%;
          aspect-ratio: 580 / 327;

          &:focus, &:focus-visible {
            outline-offset: -4px;
            outline: 2px solid $grey-222;
            border-radius: 10px;
          }

          img {
            display: block;
            width: 100%;
          }
        }
      }
    }
  }
}

@media (max-width: $breakpoint-xlarge) {
  .main-contents {
    // --------------------------- 메인 공통 ---------------------------
    .section-title {
      font-size: 26px;
    }

    .section-header {
      position: relative;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 27px;

      .btn-more {
        position: absolute;
        right: 0;
        top: 0;
      }

      .section-filter {
        margin-top: 23px;
      }
    }

    .section-filter {
      gap: 10px 20px;
      width: 100%;
      justify-content: center;

      .item {
        font-size: 18px;
      }
    }
    // ---------------------------------------------------------------
    .section-visual {
      padding-top: 0;
      padding-bottom: 40px;

      .inner {
        padding: 0;
      }

      .row {
        flex-direction: column;
      }

      .box-banner {
        width: 100%;
        margin-bottom: 40px;

        .banner-slider {
          .link {
            border-radius: 0;
          }
        }
      }

      .box-search {
        width: 100%;
        padding: 0 15px;

        .main-search {
          margin-bottom: 15px;
        }

        .search-header {
          margin-bottom: 12px;

          .txt-box {
            .txt {
              font-size: 18px;
              line-height: 27px;
            }

            .txt-lg {
              font-size: 30px;
              line-height: 45px;
            }
          }
        }

        .tag-list {
          margin-bottom: 0;

          .tag {
            height: 30px;
            line-height: 30px;
          }
        }

        .desc {
          display: none;
        }

        .partner-list {
          display: none;
        }
      }
    }

    .section-recommend {
      padding: 40px 0 30px;

      .section-body {
        padding: 0;

        .card-list-slider {
          padding: 0;
        }
      }
    }

    .section-exhibition {
      padding: 30px 0;
    }

    .section-education {
      padding: 30px 0;

      &::before {
        content: none;
      }
    }

    .section-meta {
      background-color: #f1fafb;

      .row {
        flex-direction: column;
      }

      .box-map {
        width: 100%;
      }

      .box-meta {
        width: 100%;
        padding: 0;

        &::before {
          content: '';
          display: block;
          width: 19px;
          height: 17px;
          background-image: url(../images/main/ico-double-arrow.png);
          background-size: cover;
          margin: 36px auto 37px;
        }

        .title {
          font-size: 24px;
          line-height: 36px;
          margin-bottom: 3px;
        }

        .slider-wrapper {
          width: calc(100% - 30px);
          margin: 0 auto;
          padding: 0;
        }

        .btn-circle-prev {
          left: -20px;
        }

        .btn-circle-next {
          right: -20px;
        }
      }
    }

    .section-notice {
      padding-top: 20px;

      .row {
        flex-direction: column;
      }

      .box-notice {
        width: 100%;
        border-bottom: 0;
        margin-bottom: 38px;

        .tab-container {
          .tabs-box {
            width: 100%;
            margin-bottom: 18px;

            .tabs {
              flex: 1;

              .item {
                height: 67px;

                .tab-button {
                  font-size: 20px;
                }

                &.on {
                  .tab-button {
                    font-size: 26px;
                  }
                }
              }
            }
          }
        }

        .alert-list {
          margin-bottom: 18px;

          .item {
            + .item {
              margin-top: 8px;
            }

            a {
              padding: 5px 0;
            }
          }
        }

        .btns {
          display: none;
        }
      }

      .box-event {
        width: 100%;
        margin-left: 0;
        padding-right: 0;

        .title-head {
          height: auto;
          margin-bottom: 11px;
        }
      }
    }
  }
}

@media (max-width: $breakpoint-medium) {
  .main-contents {
    .section-notice {
      .alert-list {
        .item {
          .txt {
            max-width: calc(100% - 50px - 15px);
          }

          .date {
            display: none;
          }
        }
      }
    }
  }
}